<!--  -->
<template>
  <div>
   <Zi bool1="true"  goback="true" data="<" con="订单列表">
   <h1 @click="$router.go(-1)" slot="left">
        <
      </h1>
   </Zi>
   <div class="order">
      <ul>
        <li>
         <div class="order_left"><img data-v-c8684834="" src="//elm.cangdu.org/img/164ad0b6a3917599.jpg" ></div>
         <div class="order_right">
           <div class="order_right_top">
             <h3>效果演练</h3>
             <span>></span>

             <p>等待支付</p>
           </div>
             <div class="order_right_cen1">
             <p>2019</p>
           </div>
           <div class="order_right_cen2">
             <p>111111111</p>
             <b>89</b>
           </div>
           <div class="order_right_botton">
                  去支付(还剩{})
           </div>
         </div>
        </li>
        
      </ul>
   </div>
      <Foot></Foot>
  </div>
</template>

<script>

import Zi from "../../components/header"
import Foot from "../../components/foot"
export default {
  data () {
    return {
      user_id:"",
      time:15*60
    };
  },

  components: {Zi,Foot},

  computed: {},
beforeMount(){
    setInterval(() => {
        this.time--
    }, 1000);
    // this.time=parseInt(this.time/60)

  if(localStorage.getItem("user_id")){
     this.user_id=JSON.parse(localStorage.getItem("user_id"))
    }
},
  mounted(){
    var that=this
    this.http.get("https://elm.cangdu.org/bos/v2/users/"+that.user_id+"/orders?limit=10&offset=0").then((d)=>{
      console.log(d)
    })
  },

  created(){},

  methods: {}
}

</script>
<style scoped lang="scss">
.order{
  margin-top: 1rem;
  ul{
    li{
     
      height: 2.1rem;
      margin-bottom: 0.2rem;
      background: white;
      .order_left{
        float: left;
        width: 26%;
        img{
          margin: 0.2rem;
         height: 1rem;
        }
      }
      .order_right{
        float: left;
        width: 70%;
        .order_right_top{
          line-height: 0.6rem;
          overflow: hidden;
          height: 0.6rem;
        h3,span{
          float: left;
        }
        span{color: #ccc}
    
        p{float: right;}
            }
      }
      .order_right_cen1{
        line-height: 0.4rem;
        color: #d4d4d4;
        border-bottom: 1px solid #d4d4d4;
        font-size: 0.2rem;
        margin-top: -0.2rem;
      }
      .order_right_cen2{
        border-bottom: 1px solid #d4d4d4;
          line-height: 0.6rem;
          height: 0.6rem;
          display: flex;
          p{
              width: 70%;
              color: #ccc;
          }
          b{width: 30%;color: orangered;text-align: right}
      }
      .order_right_botton{
       margin-top: 0.2rem;
        color: yellow;
        float: right;
        border: 1px solid yellow;

      }
    }
  }
}
</style>


